// create an immediately invoked functional expression to wrap our code
(function() {
  // let queryString = document.querySelector('script[src*="xerodesk.js"]');
  // let querySource = queryString.getAttribute("src");
  // let queryParams = querySource.split("?")[1];
  // let queryInJSON = JSON.parse('{"' + queryParams.replace(/&/g, '","').replace(/=/g, '":"') + '"}');
  // const LIVECHAT_KEY = queryInJSON.key;
  const IS_DEVELOPMENT = process.env.NODE_ENV === "development" ? true : false;
  const BASE_URL = IS_DEVELOPMENT ? process.env.MIX_LOCAL_URL : process.env.MIX_PROD_URL;

  // define our constructor
  this.Xerodesk = function() {
    let options = {
      payload: JSON.stringify({}),
      ...arguments[0]
    };

    let wrapper = document.createElement("div");
    wrapper.setAttribute("id", "xerodesk-wrapper");
    if (IS_DEVELOPMENT) wrapper.setAttribute("class", "open");
    document.body.appendChild(wrapper);

    let style = document.createElement("style");
    style.innerText = `
      #xerodesk-wrapper #xerodesk-content {
        background: #ffffff;
        box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
        position: fixed;
        right: 0px;
        bottom: 0px;
        height: 100%;
        width: 100%;
        overflow: hidden;
        z-index: 99999;

        visibility: hidden;
        opacity: 0;
        transform: translateY(25px);
        transition: all 250ms ease-in-out;
      }
      #xerodesk-wrapper #xerodesk-content iframe {
        position: fixed;
      }
      #xerodesk-wrapper #xerodesk-toggler {
        background: #f68a1f;
        border-radius: 10px 40px 40px 40px;
        box-sizing: border-box;
        right: 20px;
        bottom: 20px;
        cursor: pointer;
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 60px;
        width: 60px;
        padding: 0.75rem;
      }
      #xerodesk-wrapper #xerodesk-toggler img {
        width: 30px;
      }
      #xerodesk-wrapper.open #xerodesk-content {
        visibility: visible;
        opacity: 1;
        transform: translateY(0px);
      }
      #xerodesk-wrapper.open #xerodesk-toggler {
        background: #c16203;
        border-radius: 100%;
        top: 20px;
        right: 20px;
        height: 30px;
        width: 30px;
        z-index: 100000;
      }
      #xerodesk-wrapper.open #xerodesk-toggler img {
        width: 10px;
      }

      @media (min-width: 576px) {
        #xerodesk-wrapper #xerodesk-content {
          border-radius: 5px;
          right: 20px;
          bottom: 100px;
          height: calc(100% - 200px);
          width: 380px;
        }
        #xerodesk-wrapper #xerodesk-toggler {
          transition: all 250ms ease-in-out;
        }
        #xerodesk-wrapper #xerodesk-toggler img {}
        #xerodesk-wrapper.open #xerodesk-content {}
        #xerodesk-wrapper.open #xerodesk-toggler {
          background: #f68a1f;
          border-radius: 40px 10px 40px 40px;
          height: 60px;
          width: 60px;
          top: auto;
        }
        #xerodesk-wrapper.open #xerodesk-toggler img {
          width: 20px;
        }
      }
      /* @media (min-width: 768px) {} */
      /* @media (min-width: 768px) {} */
      /* @media (min-width: 992px) {} */
      /* @media (min-width: 1200px) {} */
    `;
    wrapper.appendChild(style);

    let content = document.createElement("div");
    content.setAttribute("id", "xerodesk-content");
    wrapper.appendChild(content);

    let iframe = document.createElement("iframe");
    iframe.setAttribute("src", `${BASE_URL}/iframe?payload=${options.payload}`);
    iframe.setAttribute("height", "100%");
    iframe.setAttribute("width", "100%");
    iframe.setAttribute("frameborder", "0");
    content.appendChild(iframe);

    let toggler = document.createElement("div");
    toggler.setAttribute("id", "xerodesk-toggler");
    toggler.onclick = function() {
      wrapper.classList.toggle("open");

      if (wrapper.classList.contains("open")) icon.src = `${BASE_URL}/images/widget/toggler-close.svg`;
      else icon.src = `${BASE_URL}/images/widget/toggler-open.svg`;
    };
    wrapper.appendChild(toggler);

    let icon = document.createElement("img");
    icon.setAttribute("src", `${BASE_URL}/images/widget/toggler-${IS_DEVELOPMENT ? "close" : "open"}.svg`);
    toggler.appendChild(icon);
  };

  new Xerodesk();
})();
